import { isBrowser } from "../../client";
import { isKeyArrow, isKeyTab } from "../keyboard/identifiers";

var KeyListener = function KeyListener() {
  var _this = this;

  this.isTabPressed = false;
  this.isArrowPressed = false;

  if (isBrowser) {
    window.addEventListener('keydown', function (e) {
      _this.isTabPressed = isKeyTab(e);
      _this.isArrowPressed = isKeyArrow(e);
    });
    window.addEventListener('mousedown', function () {
      _this.isTabPressed = false;
      _this.isArrowPressed = false;
    });
  }
};

export var keyListener = new KeyListener();